@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin iPad {
  @media (min-width: 600px) {
    @content;
  }
}

@mixin hd {
  @media (min-width: 1304px) {
    @content;
  }
}

@mixin fullHD {
  @media (min-width: 1800px) {
    @content;
  }
}
